<div class="row matchItem"><div class="col-12 col-md-12 col-lg-3"><div class="form-group"><label class="boldText">{{name}}</label><p class="greyColor" ref="oldData">{{localField.name}}</p></div></div><div class="col-12 col-md-12 col-lg-3"><div class="form-group"><p class="greyColor" ref="newData">{{ localRekField.name ? localRekField.name : localField.name}}</p></div></div><div class="col-12 col-md-12 col-lg-3"><div class="form-group position-relative"><p class="greyColor">{{parser.name}}</p></div></div><div class="col-12 col-md-12 col-lg-3"><div class="matchControls"><ul class="d-flex"><li class="finalStatus" :class="result.status">{{result.text}}</li><div class="d-flex" v-if="result.status==='error'"><button class="btnCheck" title="Заменить на исходные данные" @click="$_vision_company_match_item_changeData(1)"><img src="@/assets/images/check.svg"></button><button class="btnCancel" title="Заменить на найденные данные" @click="$_vision_company_match_item_changeData(0)"><img src="@/assets/images/cancel.svg"></button></div><div class="d-flex ms-2" v-else-if="result.status==='check'"><img src="@/assets/images/restore.svg" @click="$_vision_company_match_item_restoreData"></div></ul></div></div></div>